// FLEX HELPERS
.flex {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  @media screen and (min-width: 48em) {
    flex-direction: row;
    flex-wrap: nowrap;
  }
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  &-xs {
    display: flex;
    flex-direction: column;

    @media screen and (min-width: 48em) {
      flex-direction: row;
    }
  }
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-start {
  display: flex;
  justify-content: flex-start;
}

.flex-end {
  display: flex;
  justify-content: flex-end;
}

.space-between {
  justify-content: space-between;
}

.space-around {
  justify-content: space-around;
}

.flex-grow {
  flex-grow: 1;
}

.align-items-center {
  display: flex;
  align-items: center;
}

.align-items-start {
  display: flex;
  align-items: start;
}

.align-self-start {
  align-self: flex-start;
}

.truncate {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

// POSITION HELPERS
.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.fixed {
  position: fixed;
}

.top-0 {
  top: 0;
}

.top-header-height {
  top: 55px;

  @media screen and (min-width: 48em) {
    top: 12px;
  }
}

.z-index-9 {
  z-index: 9;
}

.z-index-99 {
  z-index: 99;
}

.z-index-999 {
  z-index: 999;
}

.z-index-9999 {
  z-index: 9999;
}

.z-index-99999 {
  z-index: 99999;
}

.hidden {
  display: none;
}

.block {
  display: block;
}
