.logobar{
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 1rem;

  .logobar--logo {
    img {
      height: 50px;
      padding: 1rem;
    }
  }

  .logobar--social a {
    height: 86px;

    display: flex;
    flex-direction: row;
    align-items: center;

    color: #19227d;

    text-decoration: none;

    img {
      width: 35px;
      padding: 1rem;
    }

    span {
      font-weight: bold;
      font-size: 0.9rem;
    }
  }
}


@media screen and (min-width: 780px) {
  .logobar {
    flex-direction: row;
    justify-content: space-around;
  }
}

