main ul {
  list-style: none;
  padding-inline-start: 1rem;

  li {
    display: flex;
    flex-direction: row;
    line-height: 1.8rem;
    padding-bottom: .75rem;

    &::before {
      content: "\2022";
      color: $color-primary;
      font-weight: bold;
      margin-right: .5rem;
      margin-left: -1rem;
    }

    > * {
      justify-content: space-between;
      margin-top: 0;
    }
  }
}

.no-bullets::before {
  content: unset;
}
