.contact_overlay {
  display: block;
  position: fixed;
  top: 20vh;
  right: 0;
  z-index: 9999;

  &__buttons {
    position: relative;
    font-size: 1.8rem;
    background-color: $color-black;
    color: $color-white;
    border-radius: 1.6rem 0 0 1.6rem;
    box-shadow: 0 0 8px 1px rgba(0,0,0,0.1);

    transition: left 0.2s ease-out;
    left: calc(100% - 3rem);

    &:hover {
      left: 0;
    }

    a {
      display: flex;
      align-items: center;
      padding: .6rem;
      text-decoration: none;
      color: $color-white;
      background-color: $color-black;
      border-radius: 1.5rem 0 0 1.5rem;

      &:hover {
        background-color: lighten($color-black, 10%);
        border-radius: 0;

        &:first-child {
          border-radius: 1.5rem 0 0 0;
        }

        &:last-child {
          border-radius: 0 0 0 1.5rem;
        }
      }

      i {
        display: inline-block;
        padding-right: .3rem;
        width: 2.5rem;
        text-align: center;
      }

      span {
        font-size: 16px;
      }
    }
  }
}
