  fieldset {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    padding: 2em;
    background-color: $color-tertiary;
    border: none;
  }

  label {
    margin-bottom: .2em;

    &.required:after {
      content: " *";
    }
  }

  input,
  textarea {
    width: 93%;
    font-size: 1em;
    min-height: 2.5rem;
    margin-bottom: 1em;
    padding: .25rem .75rem;
    border: none;
    border-radius: 0;
    font-family: inherit;

    &[type=date]:invalid::-webkit-datetime-edit {
      color: $placeholder;
    }
  }

  textarea {
    padding-top: 1rem;
  }

  form {
    @media screen and (max-width: 767px) {
      margin-right: -2rem;
      margin-left: -2rem;
    }

    section {
      display: flex;
      flex-direction: row;
    }

    p {
      display: flex;
      flex-direction: row;
      align-items: center;

      &:last-child {
        padding-left: 1em;
      }

      input {
        margin: 0;
        width: 1.5rem;
      }

      label {
        padding-left: .5em;
      }
    }
  }
