@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&family=Open+Sans:wght@300;400;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');

$font-body: 'Roboto', Verdana, Helvetica, Arial, sans-serif;
$font-button: 'Amatic SC', 'Open Sans', Verdana, Helvetica, sans-serif;

h1 {
  font-size: 3rem;
  font-weight: 400;
  letter-spacing: -1px;
  line-height: 1.1;
  color: $color-tertiary;

  @media screen and (min-width: 48em) {
    font-size: 5rem;
  }
}

h2 {
  font-size: 1.75rem;
  font-weight: 400;
  color: $color-primary;
  margin: 0;
}

h3 {
  font-size: 1.5rem;
  font-weight: 400;
  color: $color-primary;
}

h4, h5 {
  line-height: 1.6;
  color: $color-primary;
}

p {
  font-weight: 300;
  line-height: 1.6;
  margin-bottom: 0;
}

button {
  font-size: 1.5rem;
  font-weight: 700;
}

figcaption {
  font-size: 1.2rem;
  font-weight: 400;
  text-align: center;
  color: $color-primary;
}

blockquote {
  font-size: 2.2rem;
  font-weight: 300;
  font-style: italic;
}

address {
  font-style: normal;
}
