header {
  position: sticky;
  top: 0;
  z-index: 999;

  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 100vw;
  border-bottom: 0.5rem solid $color-border;
  flex-wrap: wrap;

  background-color: $color-white;
}

@media screen and (min-width: 721px) {
  .hamburger-icon {
    display: none !important;
    visibility: hidden;
  }
}

header {
  @media screen and (min-width: 721px) and (max-width: 890px) {
    flex-direction: column;
    justify-content: center;
  }

  .nav-logo {
    display: block;
    height: 75px;
    padding-left: 15px;
    padding-right: 15px;
    position: relative;
    width: 91px; // Width of the logo, which is positioned absolute. Needed for flex-wrap.

    img {
      position: absolute;
      bottom: -3px;
      height: 100%;
    }
  }
}
