@media screen and (min-width: 721px) {
  nav.navbar {
    position: relative;
    background-color: $color-white;
    letter-spacing: 0;
    line-height: 1.2;
    z-index: 9999;

    height: 75px;

    a[href=''] {
      color: lighten($color-black, 45%) !important;

      cursor: default;
    }

    ul {
      display: flex;
      height: 100%;
      flex-direction: row;
      flex-wrap: nowrap;
      align-items: center;
      margin: 0;
      list-style: none;
      padding-right: 2rem;

      li.top {
        color: $color-black;
        position: relative;
        padding: 2rem 1rem;

        ul.children {
          display: none;
          flex-direction: column;
          padding: 0;
          font-size: 14px;
          line-height: 1.3;
          align-items: flex-start;
        }

        &:hover {
          ul.children {
            display: flex;
            position: absolute;
            top: 55px;
            left: 0;
            z-index: 99;
            padding-top: 1.5rem;
          }

          > a, > a:visited {
            color: $color-primary;
          }
        }

        a, a:visited {
          color: $color-black;
          text-decoration: none;
        }

        // Highlight the a and the active child of an active li.top
        &.active {
          > a, li.child.active {
          font-weight: bolder;
          }
        }

        li.child {
          padding: .5rem 1rem;

          width: 200px;
          background-color: #d8f1b2;

          &:hover {
            font-weight: bolder;
          }
        }
      }
    }
  }
}
