.side_navigation {
  height: 100%;
  background-color: $color-light-green;

  ul {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    padding: 2rem;
    padding-left: 4rem;
    height: 100%;

    li {
      color: $color-black;
      padding: .25rem 0;

      &::before {
        content: none;
      }

      a {
        color: $color-black;
        text-decoration: none;
        padding: .25rem .5rem;

        &:hover, &:active {
          font-weight: bolder;
        }
      }
    }
  }
}
