.logo {
  position: relative;
  bottom: -8px;
  height: $height-header-xs;

  @media screen and (min-width: 48em) {
    height: $height-header-md;
  }
}

.image {
  &__header {
    &--background {
      background-image: url("/assets/images/home/headerfoto-Schuytse-Tandartsen-Arnhem-tiny.jpg");
      background-position: center center;
      background-size: cover;
      overflow: hidden;
    }
  }

  &--background {
    // background-image: url("/assets/images/divider-tandarts-kind-Schuytse-Tandartsen-Arnhem.jpg");
    background-position: center center;
    background-size: cover;
    height: 200px;
  }

  &--index {
    position: absolute;
    top: 5rem;
    right: -12rem;
    height: 44rem;
  }

  &--usp {
    width: auto;
    height: 12rem;

    @media screen and (min-width: 48em) {
      height: 18rem !important;
    }
  }
}

figure.usp {
   -webkit-box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
   box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
   justify-content: flex-start;
   align-items: center;
   margin: 0;
   margin-right: 1rem;

  picture {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 18rem;
    max-height: 18rem;

    @media screen and (min-width: 48em) {
      width: 18rem;
    }
  }

  figcaption {
    width: auto;
    color: $color-black;
    text-align: left;

    h5 {
      font-size: 18px;
      font-weight: 400;
    }

    p {
      display: flex;
      flex: 1;
      font-size: 14px;
    }
  }
}

svg path {
  position: relative;
  top: -1rem;
  z-index: 99;
  stroke: none;

  &.grey {
    fill: $color-tertiary;
  }

  &.white {
    fill: $color-white;
  }
}
